@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";

  font-display: swap;
  src:
    url("../fonts/.woff2") format("woff2"),
    url("../fonts/.woff") format("woff");
}

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono.woff2") format("woff2"), url("../fonts/PTMono.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: 400;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensedBold.woff2") format("woff2"), url("../fonts/RobotoCondensedBold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: 400;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed.woff2") format("woff2"), url("../fonts/RobotoCondensed.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: 400;
}
