.about {
  padding-top: 120px;
  padding-bottom: 100px;

  @include vp-1023 {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  @include vp-767 {
    font-size: 50px;
    line-height: 80px;
  }
}

.about img {
  @include vp-767 {
    display: none;
  }
}

.about h2 {
  margin-top: 0;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-bottom: 23px;

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about p {
  font-family: $ff-ptmono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  &:last-of-type {
    margin-bottom: 20px;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}

.about__container {
  display: flex;
  gap: 100px;
  align-items: flex-start;

  @include vp-1023 {
    gap: 30px;
  }
}

.moretext-js {
  display: none;
}
