@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2"), url("../fonts/.woff") format("woff");
}
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono.woff2") format("woff2"), url("../fonts/PTMono.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: 400;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensedBold.woff2") format("woff2"), url("../fonts/RobotoCondensedBold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: 400;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed.woff2") format("woff2"), url("../fonts/RobotoCondensed.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: 400;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 350px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 50px;
    max-width: 1222px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 25px;
    min-width: 670px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
    min-width: 270px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #00b2ff;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.index-header {
  position: sticky;
  background: linear-gradient(180deg, #041827 90%, rgba(4, 24, 39, 0) 150%);
  width: 100%;
  min-height: 120px;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .index-header {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .index-header {
    min-height: 70px;
  }
}

.index-header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f2f5f6;
}

@media (max-width: 1023px) {
  .index-header__logo-wrapper {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .index-header__logo-wrapper p {
    display: none;
  }
}

.index-header__logo-wrapper svg:first-of-type {
  display: block;
}
@media (max-width: 1023px) {
  .index-header__logo-wrapper svg:first-of-type {
    display: block;
  }
}
@media (max-width: 767px) {
  .index-header__logo-wrapper svg:first-of-type {
    display: none;
  }
}

.index-header__logo-wrapper svg:last-of-type {
  display: none;
}
@media (max-width: 1023px) {
  .index-header__logo-wrapper svg:last-of-type {
    display: none;
  }
}
@media (max-width: 767px) {
  .index-header__logo-wrapper svg:last-of-type {
    display: block;
  }
}

.index-header__constacts-wrapper {
  display: inline-flex;
  gap: 97px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .index-header__constacts-wrapper {
    gap: 20px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .index-header__constacts-wrapper {
    margin-right: 10px;
  }
}
.index-header__constacts-wrapper a {
  color: #f2f5f6;
  text-decoration: none;
  padding: 10px;
}
.index-header__constacts-wrapper svg {
  vertical-align: middle;
}

@media (max-width: 1023px) {
  .index-header__constacts-wrapper span {
    display: none;
  }
}

.index-header__button {
  margin-right: 60px;
}

.logo {
  display: inline-flex;
  gap: 15px;
  align-items: center;
}
.logo p {
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
}
.logo span {
  font-size: 12px;
  line-height: 14px;
}

.logo--footer {
  align-items: normal;
}
.logo--footer p {
  margin-top: 0;
}

.button {
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  min-width: 160px;
  min-height: 40px;
  align-self: center;
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  background: transparent;
  color: #f2f5f6;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .button {
    display: none;
  }
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    background: #ffffff;
    color: #000000;
  }
}
.button.focus-visible:focus {
  background: #ffffff;
  color: #000000;
}

.button--intro {
  font-size: 18px;
  line-height: 21px;
  background: #00b2ff;
  border: none;
  padding: 15px 25px;
  min-width: 410px;
  min-height: 55px;
  display: block;
}
@media (max-width: 1023px) {
  .button--intro {
    display: block;
  }
}
@media (max-width: 767px) {
  .button--intro {
    min-width: 270px;
    min-height: 40px;
    font-size: 14px;
    line-height: 16px;
  }
  .button--intro span {
    display: none;
  }
}

.button--form {
  display: block;
  min-width: 265px;
  background-color: #000000;
  border: none;
}
@media (max-width: 1023px) {
  .button--form {
    display: block;
  }
}
@media (max-width: 767px) {
  .button--form {
    min-height: 45px;
  }
}

.button--about {
  min-width: 180px;
  color: #00b2ff;
  border-color: #00b2ff;
}
@media (max-width: 1023px) {
  .button--about {
    display: block;
  }
}
@media (max-width: 767px) {
  .button--about {
    min-width: 160px;
  }
}

.index-main {
  width: 100%;
}

.intro {
  padding-top: 185px;
  padding-bottom: 285px;
  position: relative;
  background-image: url("../img/bg-big.png");
