.features-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  column-gap: 30px;
  padding: 0;
  list-style: none;
  margin: 0;
}
@media (max-width: 767px) {
  .features-list {
    grid-template-columns: min-content 1fr min-content;
    row-gap: 30px;
    column-gap: 0;
  }
}

.features-list__item {
  border-left: 5px solid #00b2ff;
  padding-top: 40px;
  padding-left: 45px;
  padding-right: 45px;
  fill: #00b2ff;
  min-height: 300px;
}
@media (hover: hover) {
  .features-list__item:hover:not(.focus-visible) {
    background-color: #00b2ff;
  }
  .features-list__item:hover:not(.focus-visible) h3,
.features-list__item:hover:not(.focus-visible) p {
    color: #f2f5f6;
  }
  .features-list__item:hover:not(.focus-visible) svg {
    fill: #f2f5f6;
  }
}
.features-list__item.focus-visible:focus {
  background-color: #00b2ff;
}
.features-list__item.focus-visible:focus h3,
.features-list__item.focus-visible:focus p {
  color: #f2f5f6;
}
.features-list__item.focus-visible:focus svg {
  fill: #f2f5f6;
}
@media (max-width: 767px) {
  .features-list__item {
    grid-column-start: 2;
    grid-column-end: 3;
    padding-left: 30px;
    padding-right: 30px;
    min-height: 340px;
  }
}

.features-list h3 {
  margin-top: 20px;
  margin-bottom: 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #00b2ff;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;
}
@media (max-width: 767px) {
  .features-list h3 {
    font-size: 20px;
  }
}

.features-list p {
  word-wrap: break-word;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
}
@media (max-width: 767px) {
  .features-list p {
    font-size: 14px;
    line-height: 19px;
  }
}

