    width: 100%;
    text-align: left;
    border-width: 100%;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 0;
    /* Добавить цвет фона для кнопки, если она нажата (добавить .активный класс с JS), а при наведении на него курсора мыши (наведите курсор) */
