.button {
  cursor: pointer;
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  min-width: 160px;
  min-height: 40px;
  align-self: center;
  display: inline-block;
  padding: 10px 15px;
  border: 1px solid $color-font-grey;
  border-radius: 3px;
  background: transparent;
  color: $color-font-grey;
  text-transform: uppercase;

  & {
    @include vp-1023 {
      display: none;
    }
  }

  @include hover-focus {
    background: $color-default-white;
    color: $color-default-black;
  }
}

.button--intro {
  font-size: 18px;
  line-height: 21px;
  background: $color-neon-blue;
  border: none;
  padding: 15px 25px;
  min-width: 410px;
  min-height: 55px;
  display: block;


  @include vp-1023 {
    display: block;
  }

  @include vp-767 {
    min-width: 270px;
    min-height: 40px;
    font-size: 14px;
    line-height: 16px;

    & span {
      display: none;
    }
  }
}

.button--form {
  display: block;
  min-width: 265px;
  background-color: $color-default-black;
  border: none;

  & {
    @include vp-1023 {
      display: block;
    }

    @include vp-767 {
      min-height: 45px;
    }
  }
}

.button--about {
  min-width: 180px;
  color: $color-neon-blue;
  border-color: $color-neon-blue;

  @include vp-1023 {
    display: block;
  }

  @include vp-767 {
    min-width: 160px;
  }
}
