.accordion__button {
  font-family: $ff-robotocondensed;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  background: none;
  border: none;
  padding-left: 0;
  margin-bottom: 20px;
  transition: 0.4s;

  @include vp-767 {
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
    width: 100%;
    text-align: left;
    border-width: 100%;
    padding-left: 25px;
    padding-right: 25px;
    margin-bottom: 0;

    /* Добавить цвет фона для кнопки, если она нажата (добавить .активный класс с JS), а при наведении на него курсора мыши (наведите курсор) */
    .active,
    &:hover {
      background-color: #ccc;
    }

    &:after {
      content: url(../img/svg/plus.svg);
      font-size: 13px;
      color: #f8fdff;
      float: right;
      margin-left: 5px;
    }

    .active__button:after {
      content: url(../img/svg/minus.svg);
    }
  }
}

/* Стиль аккордеон панели. Примечание: скрыто по умолчанию */
.accordion__panel {
  @include vp-767 {
    padding: 0 18px;
    background-color: $bg-color-dark;
    display: none;
    overflow: hidden;
  }
}

.accordion__list {
  display: inline-flex;
  max-height: 140px;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  column-gap: 30px;
  padding: 0;
  margin: 0;
  color: rgba(255, 255, 255, 0.7);

  @include vp-1439 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-1023 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-767 {
    max-height: max-content;
    flex-wrap: nowrap;
    padding-left: 25px;
  }

  & li:not(:first-of-type),
  & li:not(:last-of-type) {
    margin-bottom: 10px;
  }

  & li:nth-child(4) {
    margin-bottom: 0;
  }

  & p {
    color: rgba(255, 255, 255, 0.7);
  }

  & a {
    font-family: $ff-ptmono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: rgba(255, 255, 255, 0.7);

    @include vp-767 {
      font-size: 14px;
      line-height: 25px;
    }
  }


}

.accordion--nav {
  min-width: 470px;

  @include vp-1023 {
    grid-column-start: 2;
    grid-column-end: 3;
    min-width: auto;
    grid-row-start: 2;
    grid-row-end: 3;
    max-height: max-content;
  }

  @include vp-767 {
    grid-row-start: 2;
    grid-row-end: auto;
    grid-column-start: 1;
    grid-column-end: -1;
  }
}

.accordion--contacts {

  & ul {
    flex-wrap: nowrap;
    max-height: max-content;
  }


  @include vp-1023 {
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
  }

  @include vp-767 {
    grid-row-start: 3;
    grid-row-end: auto;
    grid-column-start: 1;
    grid-column-end: -1;

  }
}
