.features-list {
  // display: flex;
  // flex-wrap: wrap;
  // gap: 30px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  column-gap: 30px;
  padding: 0;
  list-style: none;
  margin: 0;

  @include vp-767 {
    grid-template-columns: min-content 1fr min-content;
    row-gap: 30px;
    column-gap: 0;
  }
}

.features-list__item {
  border-left: 5px solid $color-neon-blue;
  padding-top: 40px;
  padding-left: 45px;
  padding-right: 45px;
  fill: $color-neon-blue;
  min-height: 300px;


  @include hover-focus {
    background-color: $color-neon-blue;

    h3,
    p {
      color: $color-font-grey;
    }
    svg {
      fill: $color-font-grey;
    }

  }

  @include vp-767 {
    grid-column-start: 2;
    grid-column-end: 3;
    padding-left: 30px;
    padding-right: 30px;
    min-height: 340px;
  }
}



.features-list h3 {
  margin-top: 20px;
  margin-bottom: 15px;
  font-family: $ff-robotocondensed;
  color: $color-neon-blue;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;

  @include vp-767 {
    font-size: 20px;
  }

  // @include hover-focus {
  //   color: $color-font-grey;
  // }
}

.features-list p {
  word-wrap: break-word;
  font-family: $ff-ptmono;
  color: $bg-color-dark;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }

  // @include hover-focus {
  //   color: $color-font-grey;
  // }
}
