.logo {
  display: inline-flex;
  gap: 15px;
  align-items: center;

  & p {
    color: $color-font-grey;
    font-family: $ff-robotocondensed;
    font-style: normal;
    font-weight: 700;
    font-size: 25px;

  }

  & span {
    font-size: 12px;
    line-height: 14px;
  }

}

.logo--footer {
  align-items: normal;

  & p {
    margin-top: 0;
  }
}
