.index-header {
  position: sticky;
  background: linear-gradient(180deg, #041827 90%, rgba(4, 24, 39, 0) 150%);

  // max-width: 1920px;
  width: 100%;
  min-height: 120px;
  flex-wrap: wrap;

  @include vp-1023 {
    min-height: 100px;
  }

  @include vp-767 {
    & {
      min-height: 70px;
    }
  }
}

.index-header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-font-grey;
}

.index-header__logo-wrapper {

  @include vp-1023 {
    margin-right: auto;
  }

  @include vp-767 {
    & p {
      display: none;
    }
  }
}

.index-header__logo-wrapper svg:first-of-type {
  display: block;

  @include vp-1023 {
    display: block;
  }

  @include vp-767 {
    display: none;
  }
}

.index-header__logo-wrapper svg:last-of-type {
  display: none;

  @include vp-1023 {
    display: none;
  }

  @include vp-767 {
    display: block;
  }
}

.index-header__constacts-wrapper {
  display: inline-flex;
  gap: 97px;
  margin-right: auto;
  margin-left: auto;

  @include vp-1023 {
    gap: 20px;
    margin-right: 20px;
  }

  @include vp-767 {
    margin-right: 10px;
  }

  & a {
    color: $color-font-grey;
    text-decoration: none;
    padding: 10px;
  }

  & svg {
    vertical-align: middle;
  }
}

.index-header__constacts-wrapper span{
  @include vp-1023 {
    display: none;
  }
}

.index-header__button {
  margin-right: 60px;
}

