.form {
  & p{
    display: flex;
    margin-bottom: 20px;

    @include vp-767 {
      gap: 20px;
      flex-wrap: wrap;
    }
  }

  & p:last-of-type {
    gap: 50px;
    margin-bottom: 0;

    @include vp-1439 {
      gap: 85px;
    }

    @include vp-767 {
      gap: 25px;
    }
  }
}

.form label{
  display: flex;
  align-items: center;
  margin-top: 15px;
  gap: 10px;

  @include vp-1439 {
    margin-top: auto;
  }

  & span {
    font-size: 14px;
    line-height: 16px;
  }
}

.form input {
  background: #019bdf;
  border: 0;
  padding: 15px;
  border-radius: 3px;
  color: $color-default-white;
  font-family: $ff-ptmono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;


  &:first-of-type {
    margin-right: auto;
  }
}

::-webkit-input-placeholder { /* WebKit browsers */
  color: $color-default-white;
}

.form [type="text"],
.form [type="tel"] {
  min-width: 275px;

  @include vp-1439 {
    min-width: 320px;
  }

  @include vp-767 {
    min-width: 270px;
  }
}

.form textarea {
  width: 100%;
  min-height: 155px;
  background: #019bdf;
  border: 0;
  padding: 15px;
  border-radius: 3px;
  color: $color-default-white;
  font-family: $ff-ptmono;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 35px;
}
