.feedback {
  width: 100%;
  height: 600px;
  background: #00B3FF;
  position: relative;
  color: $color-default-white;
  min-height: 840px;
  padding-top: 170px;
  padding-bottom: 250px;

  @include vp-1439 {
    padding-top: 50px;
    padding-bottom: 125px;
  }

  @include vp-767 {
    padding-bottom: 80px;
  }


  &::before {
    position: absolute;
    content: "";
    clip-path: polygon(0% 0%, 0% 100%, 100% 0);
    background: #026593;
    width: 50%;
    height: 600px;
    left: 0;
    top: 0;

    @include vp-1439 {
      display: none;
    }

  }
}

.feedback__container {
  display: flex;
  position: relative;
  justify-content: right;
}

.feedback__wrapper {
  position: relative;
  min-width: 580px;

  @include vp-1439 {
    width: 100%;
    margin-left: 0;
  }

  @include vp-767 {
    min-width: auto;
  }

  & span {
    font-family: $ff-ptmono;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;

    @include vp-767 {
      font-size: 14px;
      line-height: 19px;
    }
  }

  &::before {
    position: absolute;
    content: "";
    left: -850px;
    top: 76px;
    width: 510px;
    height: 285px;
    background-image: url("../img/svg/plane.svg");

    @include vp-1439 {
      display: none;
    }

  }
}



.feedback h2 {
  font-family: $ff-robotocondensed;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-bottom: 0;

  @include vp-767 {
    font-size: 30px;
    line-height: 42px;
  }
}

.feedback p {
  margin-top: 0;
}

