.index-footer {
  background: $bg-color-dark;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 65px;
}

.index-footer__container {
  display: flex;


  @include vp-1439 {
    display: grid;
    grid-template-columns: min-content 1fr 1fr min-content;
    grid-template-rows: min-content 1fr 65px;
    row-gap: 35px;
  }

  @include vp-767 {
    padding: 0;
    grid-template-columns: 1fr;
    // grid-template-rows: min-content 1fr 1fr;
    row-gap: 0;
  }
}

.index-footer__wrapper {
  margin-right: 160px;

  @include vp-1023 {
    display: flex;
    justify-content: space-between;
    margin-right: 0;
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    grid-column-start: 1;
    grid-column-end: -1;
    margin-bottom: 25px;
  }
}
